.body {
  background-color: #d4e0ee;
  font-family: "Roboto", sans-serif; }

.main {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center; }

.section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 260px;
  height: fit-content;
  background-color: white;
  border-radius: 10px;
  padding: 15px; }
  .section__img {
    width: 250px;
    height: 250px;
    border-radius: 10px; }
  .section__title {
    font-weight: bold;
    text-align: center;
    font-size: 18px;
    margin-left: 15px;
    margin-right: 15px; }
  .section__text {
    font-size: 13px;
    color: #888992;
    text-align: center;
    margin-left: 15px;
    margin-right: 15px; }
